.inputWrap {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.input {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  font-size: 0.8em;
  padding: 0.8em 1.2em;
  background-color: #f5f0f9;
  border: none;
  border-radius: 3px;
  border: 0.5px solid transparent;
}
.input::placeholder {
  color: #6a385f;
}

.errorInput {
  border: 0.5px solid #ed1b22;
}

.errorMessage {
  font-size: 0.62em;
  color: #ed1b22;
  position: absolute;
  bottom: -13px;
  left: 0;
}